@import '~assets/styles/h3Imports';

.root {
  border-radius: em(16);
  width: em(664);

  @media (max-width: #{$max-tablet-size}) {
    width: em(472);
  }

  @media (max-width: $max-phone-size) {
    margin: 0 auto;
    max-width: 100%;
    width: em(343);
  }
}

.icon-wrapper {
  display: flex;
  justify-content: center;
  margin-top: var(--spacing-m);
}

.header {
  text-align: center;

  @media (max-width: $max-phone-size) {
    font-size: em(24);
  }
}

.description {
  display: block;
}

.button {
  --button-min-width: none;

  width: em(121);

  @media (max-width: $max-phone-size) {
    width: 100%;
  }

  @media (orientation: landscape) and (max-height: $max-phone-size) {
    width: 100%;
  }
}
